import React from "react";
import "./work.css";

export default function Work({
  data: {
    imageLink,
    imageAlt,
    company,
    role,
    startTime,
    endTime,
    details,
    responsibilities,
    key,
  },
}) {
  return (
    <div className={`work-item`}>
      <img className={`company-logo`} src={imageLink} alt={imageAlt}></img>
      <div className="work-item-details">
        <h1>
          {role} at {company}
        </h1>
        <div className="time">
          {startTime} - {endTime}
        </div>
        <div className="details">{details}</div>
        {responsibilities && responsibilities.length && (
          <div>
            <h2>Responsibilities</h2>
            <ul>
              {responsibilities.map((responsibility, index) => (
                <li key={`${key}_responsability_${index}`}>{responsibility}</li>
              ))}
            </ul>
          </div>
        )}
      </div>
    </div>
  );
}
